* {
  box-sizing: border-box;
  font-family: Inter, sans-serif;
}

body {
  width: 100%;
  margin: 0;
  display: flex;
}

.back-btn {
  color: #999;
  text-decoration: none;
}

.profile-container {
  max-width: 600px;
  max-height: 700px;
  border-radius: 12px;
  margin: auto;
  position: absolute;
  inset: 0;
  box-shadow: 0 0 7px #00000080;
}

.avatar-wrap {
  width: 100%;
  height: 20%;
  align-items: center;
}

.avatar {
  width: 130px;
  height: 130px;
  background: #efefef url("https://vignette.wikia.nocookie.net/marvelcinematicuniverse/images/7/73/SMH_Mentor_6.png");
  border-radius: 50%;
  margin: 40px auto;
  display: block;
  box-shadow: 0 0 7px #00000080;
}

.name-wrap {
  text-align: center;
  font-weight: 500;
}

.info-wrap {
  border-bottom: 1px solid #eaeaea;
  margin: 0 40px;
  display: flex;
}

.info-label {
  float: left;
  font-size: 15px;
}

.info {
  float: right;
  color: #999;
  margin-left: auto;
  font-size: 15px;
}

.settings-link {
  border-bottom: 1px solid #eaeaea;
  margin: 20px 40px;
}

.settings-link a {
  color: #3369f3;
  font-size: 13px;
  font-style: normal;
  font-weight: 500;
  line-height: 13px;
  text-decoration: none;
}

.back-button {
  color: #999;
  margin: 10px;
  font-size: 19px;
}

